/* carousel */
.carousel-item-next,
.carousel-item-prev,
.carousel-item.active {
  display: flex;
}
.carousel-caption {
  position: absolute;
  top: 0;
}

.carousel-caption h2 {
  color: #fff;
}

.carousel img {
  height: 300px;
  padding: 2px;
  margin: 40px;
  margin-top: 50px;
  width: auto;
  margin-left: auto;
  margin-right: auto;
  border-radius: 18px;
}
.carousel a {
  margin: 0 auto;
}
@media (max-width: 900px) {
    .carousel-caption h2 {
      font-size: 2.5vw;
    }
  }